<template>
  <div class="rent-select" tabindex="0">
    <img v-if="isClearable && !!Object.keys(selected).length" @click="clearSelected()" :class="['clearData', dir === 'ltr' ? 'atRight' : 'atLeft']" width="15px" height="15px" src="@/assets/images/icons/clear.svg">
    <div :class="['dropdown-container', {'pl-2' : isClearable}]">
      <img
        class="dropdown-icon-container"
        width="20px"
        height="20px"
        :src="iconName"
        :style="{margin: dir === 'rtl' ? 'auto 0 auto 10px' : 'auto 10px auto 0'}"
        alt="photo" />
      <span class="w-100 overflow-hidden" v-on:click="showDropdown">{{
        selected[label] || placeholder | displayExcerpt(26)
      }}</span>
    </div>
    <b-modal
      id="modal-android"
      :hide-footer="true"
      :hide-header="true"
      centered
      title="BootstrapVue"
      v-model="showMobile">
      <div class="modal-close" v-on:click="optionsShown = !optionsShown">
        <span>X</span>
      </div>
      <div class="title-container">
        <p class="subtitle">{{ subtitle }}</p>
        <p class="title">{{ title }}</p>
      </div>
      <hr />
      <div class="search-field-container">
        <svg-icon
          class="search-icon"
          name="magnifying-glass-grey"
          width="24"
          height="24"></svg-icon>
        <input
          class="dropdown-search"
          v-model="searchQuery"
          :placeholder="searchText" />
        <span
          class="search-clear"
          v-if="searchQuery !== ''"
          v-on:click="searchQuery = ''"
          >Clear</span
        >
      </div>
      <div class="options-container">
        <div
          :class="
            selected && selected === option ? 'option selected' : 'option'
          "
          v-for="option in shownOptions"
          v-on:click="selected = option"
          :key="option.id">
          <div class="option-label">{{ option[label] }}</div>
          <div class="option-circle"><div class="inner-circle"></div></div>
        </div>
      </div>
    </b-modal>
    <div
      id="desktop-option-container"
      v-if="optionsShown && currentOS !== 'Android' && currentOS !== 'iOS'">
      <div class="search-field-container">
        <input
          class="dropdown-search"
          v-model="searchQuery"
          placeholder="Search" />
      </div>
      <div class="options-container">
        <div
          :class="
            selected && selected === option ? 'option selected' : 'option'
          "
          v-for="option in shownOptions"
          v-on:click="selected = option"
          :key="option.id">
          <div class="option-label">{{ option[label] }}</div>
          <div class="option-circle"><div class="inner-circle"></div></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'rentSelect',
  components: {},

  props: {
    iconName: {
      type: String,
      required: true,
      default: () => '',
    },
    options: {
      type: Array,
      required: false,
      default: () => [],
    },
    label: {
      type: String,
      required: false,
      default: () => 'label',
    },
    title: {
      type: String,
      required: false,
      default: () => 'Select option',
    },
    subtitle: {
      type: String,
      required: false,
      default: () => '',
    },
    searchText: {
      type: String,
      required: false,
      default: () => 'Search',
    },
    value: {
      type: String,
      required: false,
      default: () => 'value',
    },
    icon: {
      type: String,
      required: false,
    },
    placeholder: {
      type: String,
      required: false,
      default: () => 'Select an option',
    },
    multiple: {
      type: Boolean,
      required: false,
      default: () => false,
    },
    Type: {
      type: String,
      required: false,
      default: () => '',
    },
    defaultValue: {
      type: Object,
      required: false,
      default: () => {},
    },
    isClearable: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      selected: {},
      searchQuery: '',
      optionsShown: false,
      shownOptions: [],
    };
  },
  methods: {
    showDropdown() {
      var self = this;
      this.optionsShown = !this.optionsShown;
      if (
        this.optionsShown &&
        this.currentOS !== 'Android' &&
        this.currentOS !== 'iOS'
      ) {
        window.addEventListener('click', (e) => {
          if (
            self.optionsShown &&
            self.currentOS !== 'Android' &&
            self.currentOS !== 'iOS'
          ) {
            let container = document
              .getElementById('desktop-option-container')
              .getBoundingClientRect();
            let container2 = document
              .querySelector(`#${self.$attrs.id} .dropdown-container`)
              .getBoundingClientRect();
            if (
              (e.clientX < container.left ||
                e.clientX > container.right ||
                e.clientY < container.top ||
                e.clientY > container.bottom) &&
              (e.clientX < container2.left ||
                e.clientX > container2.right ||
                e.clientY < container2.top ||
                e.clientY > container2.bottom)
            ) {
              self.optionsShown = false;
            }
          }
        });
      }
    },
    clearSelected(){
      this.selected = {}
    }
  },
  mounted() {
    this.shownOptions = this.options;
    if (this.defaultValue) {
      this.selected = this.defaultValue;
    }
  },
  computed: {
    currentOS() {
      var userAgent = navigator.userAgent || navigator.vendor || window.opera;

      // Windows Phone must come first because its UA also contains "Android"
      if (/windows phone/i.test(userAgent)) {
        return 'Windows Phone';
      }

      if (/android/i.test(userAgent)) {
        return 'Android';
      }

      // iOS detection from: http://stackoverflow.com/a/9039885/177710
      if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
        return 'iOS';
      }

      return 'unknown';
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    showMobile: {
      get() {
        return (
          this.optionsShown &&
          (this.currentOS === 'Android' || this.currentOS === 'iOS')
        );
      },
      set(newName) {
        return newName;
      },
    },
  },
  watch: {
    selected: {
      handler(value) {
        this.optionsShown = false;
        this.$emit('input', this.selected);
      },
    },
    options: {
      handler(value) {
        this.shownOptions = value;
      },
    },
    searchQuery: {
      handler(value) {
        if (value === '') {
          this.shownOptions = this.options;
        } else {
          this.shownOptions = this.options.filter((option) => {
            return option[this.label]
              .toLowerCase()
              .includes(value.toLowerCase());
          });
        }
      },
    },
    defaultValue: {
      handler() {
        this.selected = this.defaultValue;
      },
      deep: true,
    },
  },
};
</script>

<style lang="scss">
.rent-select {
  position: relative;
  img.clearData {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    &.atRight {
      right: 10px;
    }
    &.atLeft {
      left: 10px;
    }
  }
}
select {
  display: block;
  max-width: 100%;
}
.dropdown-container {
  display: flex;
  border: 1px solid #000000;
  border-radius: 8px;
  padding: 0 15px;
  height: 44px;
  line-height: 44px;
  cursor: pointer;
  & * {
    cursor: pointer !important;
  }
}
#modal-android {
  .modal-content {
    background-color: #ffffff;
    border-radius: 10px;
    hr {
      margin: 16px -15px;
    }
    .title-container {
      .subtitle {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #48535b;
        margin-bottom: 4px;
      }
      .title {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        color: #111111;
        margin-bottom: 0;
      }
    }
    .search-field-container {
      position: relative;
      margin-bottom: 24px;
      svg {
        position: absolute;
        left: 5px;
        top: 5px;
      }
      .dropdown-search {
        width: 100%;
        background: transparent;
        color: black;
        padding: 5px 35px;
        font-size: large;
        height: 38px;
        border: 1px solid #e3e5e5;
        border-radius: 8px;
      }
    }
    .option {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 15px 0;
      &:not(:last-child) {
        border-bottom: lightgray 1px solid;
      }
      .option-circle {
        width: 20px;
        height: 20px;
        background-color: transparent;
        border-radius: 50%;
        border: 2px solid $primary;
        margin: auto 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      .option-label {
        max-width: calc(100% - 20px);
        cursor: default;
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
        color: #111111;
      }
      &.selected {
        .inner-circle {
          width: 10px;
          height: 10px;
          background: $primary;
          border-radius: 50%;
          margin: auto;
        }
      }
    }
  }
  .modal-close {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 36px;
    height: 36px;
    background: #f7f9fa;
    border-radius: 100px;
    span {
      color: black;
      font-size: large;
      font-weight: bolder;
      text-align: center;
    }
  }
}
#desktop-option-container {
  width: 92%;
  position: absolute;
  max-height: 50vh;
  background-color: white;
  z-index: 100;
  padding: 5px;
  margin-top: 5px;
  border-radius: 0 0 10px 10px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
  .search-field-container {
    padding: 15px;
    .dropdown-search {
      width: 100%;
    }
  }
  .option {
    padding: 5px 10px;
    &:hover {
      background-color: rgb(249, 249, 249);
    }
    &.selected {
      background-color: $primary;
      color: white;
    }
  }
  .option-label {
    cursor: pointer;
  }
}
</style>
