<template>
  <b-container>
    <b-row :class="['single-prop-card', 'm-2', 'my-3', isHome ? 'w-100' : '', shadow ? 'shadow' : '']">
      <b-col cols="9">
        <div>
          <h1 :class="['font-size-normal', 'font-weight-bold', 'dark-blue', 'mb-0', isHome ? 'isHome' : '']" v-b-modal="'modal-' + item.id">
            <!--    @click="test(item.id)" -->
            {{ item.name }}
          </h1>
          <span class="user_name">
            {{ '@' + item.user_name }}
          </span>
        </div>
        <!-- <pre>{{ item }}</pre> -->
      </b-col>
      <b-col cols="3" class="d-flex flex-column align-items-start p-0 m-0">
        <p :class="['gray', 'font-size-normal', 'mb-0', 'font-weight-bold', 'text-primary', isHome ? 'isHome' : '', 'd-flex align-items-center', 'makegap']">
          <span>{{ item.budget }} </span
          ><span class="font-size-small">{{ $t('common.egp') }}</span>
        </p>
        <h3 :class="['font-size-smaller', 'light-gray', isHome ? 'isHome' : '']">
          {{ item.term === 1 ? $t('common.per_month') : $t('common.per_night') }}
        </h3>
      </b-col>
      <b-col cols="12"
        ><div class="d-flex mt-3 justify-content-start">
          <div>
            <svg-icon name="calendar" height="24" width="24" />
            <span :class="['font-size-small', 'light-gray', 'font-weight-bold', isHome ? 'isHome' : '']">
              {{ item.duration + $t('common.from') }}
            </span>
            <span :class="['font-size-small', 'light-gray', 'font-weight-bold', isHome ? 'isHome' : '']">
              {{$dateFns.format(new Date(item.move_in_date), 'dd MMM yy')}}
            </span>
          </div>
        </div>
      </b-col>
      <b-col cols="12">
        <div class="d-flex mt-3 justify-content-start">
          <div>
            <!-- <svg-icon name="time" height="24" width="24" /> -->
            <span :class="['font-size-small', 'light-gray', 'font-weight-bold', isHome ? 'isHome' : '']">
              {{ item.created_at + ' |' }}
            </span>
            <span :class="['font-size-small', 'light-gray', 'font-weight-bold', isHome ? 'isHome' : '']">
              {{ item.offers_count + ' ' + $t('property.offers_submitted')}}
            </span>
          </div>
        </div>
      </b-col>
      <b-col cols="12">
        <hr>
        <div class="tags d-flex flex-wrap">
          <div class="tag text-primary">
            # {{ tag }}
          </div>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    isHome:{
      type: Boolean,
      default: false
    },
    shadow:{
      type: Boolean,
      default: false
    }
  },
  data(){
    return {
      tag: 'Residential'
    }
  }
};
</script>

<style lang="scss" scoped>
.tags {
  gap: 10px;
}
.single-prop-card {
  background: white;
  border: 1px solid #e3e5e5;
  border-radius: 1rem;
  padding: 1rem;
  .isHome {
    font-size: 12px !important;
  }
  .makegap {
    gap: 5px !important;
  }
}
</style>
