<template>
  <MarketPlaceList
    :propertiesData="propertiesData"
    :list="list"
    :query="query"
    :loading="loading" />
</template>

<script>
import { fetchPropertiesData } from '@/api/properties';
import { fetchMarketPlaceData } from '@/api/marketplace';
import MarketPlaceList from '@/components/marketplace/MarketPlaceList';
import { createNamespacedHelpers } from 'vuex';
const { mapState } = createNamespacedHelpers('UserModule');

export default {
  name: 'marketplace',
  watchQuery: true,
  components: {
    MarketPlaceList,
  },
  data() {
    return {
      list: null,
      propertiesData: {},
      loading: false,
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Marketplace" : "المتجر"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "Marketplace contains tenants requests" : "المتجر يحتوى على طلبات المستأجرين",
        }
      ]
    }
  },
  computed: {
    ...mapState(['user']),
    appEnv(){
      return this.$config.APP_ENV
    },
    query() {
      return this.$route.query;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  },
  watch: {
    '$route.query': '$fetch',
  },
  methods: {
    async getPropertiesData() {
      try {
        const { Data } = await fetchPropertiesData();
        this.propertiesData = Data;
      } catch (error) {
        console.log(error);
      }
    },
    async getMarketPlaceData() {
      try {
        const { Data } = await fetchMarketPlaceData(this.query);
        this.list = Data;
      } catch (error) {}
    },
    async getMarketPlaceDataWithDefaultLocation() {
      try {
        if(this.user.desired_location.location_id){
          let res;
          res.Data = await fetchMarketPlaceData({
            ...this.query,
            location_id: this.user.desired_location.location_id,
          });
        }else{
          res.Data = await fetchMarketPlaceData();
        }
        const { Data } = await fetchMarketPlaceData({
          ...this.query,
          location_id: this.user.desired_location.location_id,
        });
        this.list = Data;

        this.$store.dispatch('PropertyModule/getChildRoot', {
          location_id: this.user.desired_location.location_id,
          type: 'district',
        });
      } catch (error) {}
    },
  },
  async created() {
    this.loading = true;

    this.user
      ? await this.getMarketPlaceDataWithDefaultLocation()
      : await this.getMarketPlaceData();

    this.loading = false;
  },
  async fetch() {
    this.loading = true;

    await this.getPropertiesData();
    await this.getMarketPlaceData();

    this.loading = false;
  },
};
</script>

<style lang="scss" scoped></style>
