<template>
  <div class="filter-options">
    <div class="total-requests">
      {{ $t('marketplace.active_request') }} ({{ totalRequests }})
    </div>
    <div class="filters">
      <rent-select
        :isClearable="true"
        :dir="dir"
        :options="propertiesData.rootLocations"
        @input="cityInput"
        @clearFilters="clearCityFilters"
        label="location_name"
        value="location_id"
        class="destination-input"
        :placeholder="
          LocationName ? LocationName : $t('marketplace.filter_by_city')
        "
        :iconName="require(`@/assets/svg-sprite/svg/city.svg`)">
      </rent-select>

      <rent-select
        :isClearable="true"
        id="districtSelect"
        :dir="dir"
        v-if="query && query.location_id && childLocationDistrict.length"
        label="location_name"
        @clearFilters="clearDistrictFilters"
        :options="childLocationDistrict"
        @input="districtInput"
        :placeholder="$t('marketplace.filter_by_district')"
        :iconName="require(`@/assets/svg-sprite/svg/district.svg`)">
      </rent-select>
    </div>
  </div>
</template>

<script>
import rentSelect from '~/components/shared/form/rentSelect';
export default {
  props: {
    totalRequests: {
      type: Number,
      default: 0,
    },
    propertiesData: {
      type: Object,
      required: true,
    },
    query: {
      type: Object,
      required: true,
    },
  },
  components: {
    rentSelect,
  },
  computed: {
    childLocationDistrict() {
      return this.$store.state.PropertyModule.districtLocation;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    LocationName() {
      if (this.query?.location_id) {
        let locationName = this.propertiesData?.rootLocations?.filter(
          (location) => this.query?.location_id == location.location_id
        )[0]?.location_name;
        return locationName != undefined ? locationName : '';
      } else {
        return '';
      }
    },
  },
  data(){
    return {
      selectedCity: null || this.$route.query.location_id,
    }
  },
  methods: {
    cityInput(city) {
      if(Object.keys(city).length == 0){
        this.$router.push(this.localePath({
          path: ('/marketplace'),
          query: { page: 1 },
        }));
      }else{
        this.selectedCity = city.location_id;
        localStorage.setItem('selectedCityId', city.location_id)
        const location_id = city.location_id;
        if (!location_id) return;
  
        this.$store.dispatch('PropertyModule/getChildRoot', {
          location_id,
          type: 'district',
        });
  
        this.$router.push(this.localePath({
          path: '/marketplace',
          query: { ...this.query, location_id, page: 1 },
        }));
      }
    },
    districtInput(district) {
      if(Object.keys(district).length == 0){
        this.$router.push(this.localePath({
          path: '/marketplace',
          query: { location_id: this.selectedCity || localStorage.getItem('selectedCityId'), page: 1 },
        }));
      }else{
        if (!district.location_id) return;
  
        this.$router.push(this.localePath({
          path: '/marketplace',
          query: { ...this.query, location_id: district.location_id, page: 1 },
        }));
      }
    },
    clearCityFilters() {
      this.$router.push(this.localePath({
        path: '/marketplace',
        query: { ...this.query, location_id: null, page: 1 },
      }));
    },
    clearDistrictFilters() {
      this.$router.push(this.localePath({
        path: '/marketplace',
        query: { ...this.query, location_id: null, page: 1 },
      }));
    },
  },
  // created(){
  //   this.selectedCity = this.$route.query.location_id;
  // },
  // watch: {
  //   '$route.query.location_id': {
  //     handler: function(val) {
  //       this.selectedCity = val;
  //     },
  //     deep: true,
  //     immediate: true
  //   }
  // }
};
</script>

<style lang="scss">
.filter-options {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .total-requests {
    font-weight: bold;
    width: 40%;
  }

  .filters {
    display: flex;
    width: 60%;

    .rent-select {
      margin: 0 8px;
      width: 100%;

      #desktop-option-container {
        width: 450px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .filter-options {
    flex-direction: column;

    .total-requests {
      width: 100%;
    }

    .filters {
      width: 100%;
      gap: 10px;
      // flex-direction: column;

      .rent-select {
        margin: 8px 0;
        width: 100%;
      }
    }
  }
}
</style>
