<template>
  <div class="main-container-market">
    <b-modal id="selectedRequest" hide-footer>
      <SendOffer
        :selectedRequest="selectedRequest"
        :desktopView="true"
        @colseSideBar="colseSideBar" />
    </b-modal>
    <!-- <b-sidebar
      v-model="showOfferForm"
      v-if="showOfferForm"
      right
      shadow
      :key="showOfferForm">
      <SendOffer
        :selectedRequest="selectedRequest"
        :desktopView="true"
        @colseSideBar="colseSideBar" />
    </b-sidebar> -->
    <!-- Header -->
    <Header />
    <!-- Content -->
    <b-container class="mt-3">
      <!-- Filters -->
      <FilterOptions
        :propertiesData="propertiesData"
        :totalRequests="list && list.pagination.total"
        :query="query" />

      <!-- cards -->
      <b-overlay :show="loading" rounded="sm" :opacity="1">
        <b-row class="mt-3 w-100" v-if="list && list.collection">
          <b-col
            xl="3"
            lg="6"
            md="12"
            sm="12"
            class="px-0"
            v-for="item in list.collection"
            :key="item.id">
            <!-- <MarketPlaceCard :item="item" /> -->
            <MarketPlaceCard_2 :item="item" />
          </b-col>
        </b-row>

        <b-row>
          <b-col v-if="list && list.pagination">
            <b-pagination
              :value="list.pagination.current_page"
              :total-rows="list.pagination.total"
              :per-page="list.pagination.per_page"
              @change="paginateData"
              pills
              size="md"
              align="center"></b-pagination>
          </b-col>
        </b-row>

        <b-row v-if="list && list.collection">
          <b-modal
            hide-footer
            v-for="item in list.collection"
            :key="item.id"
            :id="`modal-${item.id}`"
            class="mt-2 pb-2 ttttttttttttttt">
            <template #modal-header>
              <b-button
                class="bg-transparent border-0 dark-blue text-right"
                block
                @click="$bvModal.hide(`modal-${item.id}`)">
                <img
                width="25px"
                height="25px"
                class="inactive-icon"
                :src="require('@/assets/images/sidebar/X.svg')"
                alt="photo" /></b-button>
            </template>
            <MarketPlaceItem
              :selectedRequest="item"
              @showOfferForm="showSideBar"
              mode="desktop" />
              <b-container fluid>
                <b-row
                  :style="{
                    position: 'fixed',
                    top: ' 86%',
                    right: '50%',
                    transform: 'translateX(47%)',
                    backgroundColor: '#ffffff',
                    width: '250px',
                  }">
                  <b-col cols="4">
                    <b-button
                      variant="outline-primary"
                      class="font-weight-bold"
                      type="button"
                      v-text="$t('marketplace.back')"
                      @click="$bvModal.hide(`modal-${item.id}`)">
                    </b-button>
                  </b-col>
                  <b-col cols="8">
                    <b-button
                      variant="primary"
                      type="button"
                      v-text="$t('marketplace.interested')"
                      @click="
                        () => {
                          showSideBar(item);
                        }
                      ">
                    </b-button>
                  </b-col>
                </b-row>
              </b-container>
            <template #modal-footer class="">
              <!-- <div> -->
              <b-row
                :style="{
                  position: 'fixed',
                  top: ' 87%',
                  right: '50%',
                  transform: 'translateX(50%)'
                }">
                <b-col cols="4">
                  <b-button
                    variant="outline-primary"
                    class="font-weight-bold"
                    type="button"
                    v-text="$t('marketplace.back')"
                    @click="$bvModal.hide(`modal-${item.id}`)">
                  </b-button>
                </b-col>
                <b-col cols="8">
                  <b-button
                    variant="primary"
                    type="button"
                    v-text="$t('marketplace.interested')"
                    @click="
                      () => {
                        showSideBar(item);
                      }
                    ">
                  </b-button>
                </b-col>
              </b-row>
              <!-- </div> -->
            </template>
          </b-modal>
        </b-row>
      </b-overlay>
    </b-container>
  </div>
</template>

<script>
import Header from './Header.vue';
import FilterOptions from './FilterOptions';
import MarketPlaceCard from './MarketPlaceCard';
import MarketPlaceCard_2 from './MarketPlaceCard_2';
import MarketPlaceItem from '@/components/marketplace/MarketPlaceItem.vue';
import SendOffer from '@/components/marketplace/SendOffer.vue';
import { createNamespacedHelpers } from 'vuex';
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule');
const { mapState } = createNamespacedHelpers('UserModule');

export default {
  name: 'MarketPlaceList',
  components: {
    Header,
    FilterOptions,
    MarketPlaceCard,
    MarketPlaceCard_2,
    MarketPlaceItem,
    SendOffer,
  },
  props: {
    propertiesData: {
      type: Object,
      default: () => {},
    },
    list: {
      type: Object | null,
      required: true,
    },
    query: {
      type: Object,
      required: true,
    },
    loading: Boolean,
  },
  data() {
    return {
      showOfferForm: false,
      selectedRequest: {},
    };
  },
  computed: {
    ...mapState(['user']),
  },
  methods: {
    ...mapAuthActions(['showAuthModal']),
    showSideBar(value) {
      if (this.user) {
        this.selectedRequest = { ...value };
        // this.showOfferForm = true;
        this.$bvModal.show('selectedRequest')
        this.$bvModal.hide(`modal-${value.id}`);
      } else {
        this.showAuthModal({
          callbackAction: {},
        });
      }
    },
    colseSideBar() {
      this.showOfferForm = false;
      this.$bvModal.hide('selectedRequest')
    },
    paginateData(page) {
      this.$router.push({
        path: this.localePath('/marketplace'),
        query: { ...this.query, page },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
::v-deep() .custom-control.custom-checkbox {
  width: 100%;

  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
  background: linear-gradient(
      0deg,
      rgba(38, 45, 61, 0.04),
      rgba(38, 45, 61, 0.04)
    ),
    #ffffff;

  span: {
    color: #0f256e !important;
    font-weight: bold;
    margin-left: 1.5rem;
    margin-bottom: 0 !important;
  }
}

::v-deep() .custom-control-label span {
  color: #0f256e !important;
  font-weight: bold;
  margin-left: 1.5rem;
  margin-bottom: 0 !important;
}
::v-deep() .custom-control-label::before {
  left: 0;
}

::v-deep() .chips-container .chip.selected .chip-label {
  color: white !important;
}
::v-deep() .vs__dropdown-menu {
  z-index: 10000000;
}

::v-deep() .dropdown-menu {
  z-index: 10030 !important;
}

::v-deep() .counter-input-contianer {
  padding: 2px 5px !important;
}

.user_name {
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #48535b;
}

</style>
